* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  html,
  body {
    background: #000 url('https://res.cloudinary.com/pruebasreact/image/upload/v1612491341/bg_ufotzd.jpg') no-repeat center center/cover;
    height: 100vh;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  a {
    text-decoration: none;
  }
  
  .container {
    max-width: 380px;
    margin: auto;
    padding: 0 20px;
  }
  
  .btn {
    display: inline-block;
    color: #fff;
    background-color: #3fb573;
    font-size: 1em;
    text-align: center;
    padding: 10px 15px;
    border: 0;
    margin: 10px 0;
  }
  
  header {
    height: 200px;
  }
  
  header img {
    width: 200px;
  }
  
  .center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  
  .card {
    cursor: pointer;
    background-color: transparent;
    height: 350px;
    perspective: 1000px;
  }
  
  .card h1 {
    font-size: 25px;
    text-align: center;
    border-bottom: 1px #fff solid;
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
  
  .card img {
    border: 1px solid green;
    border-radius: 8px;
    width: 100%;
    height: 350px;

  }
  
  .card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.9s;
    transform-style: preserve-3d;
  }
  
  .card:hover .card-inner {
    transform: rotateY(180deg);
  }
  
  .card-front,
  .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .card-back {
    background-color: grey;
    color: white;
    padding: 20px;
    transform: rotateY(180deg);
    border: 2px solid green;
    border-radius: 8px;
  }
  
  .card li {
    list-style: none;
    padding-bottom: 10px;
    padding-top: 10px;
  }

  .btn{
    margin-top: 20px;
    border-radius: 16px;
    width: 100%;
  }
  
